
body{
  width: 100%;
  height:100%;
}

.App {
  text-align: center;
  width:100%;
  height:100%;
}

.appContainer{
  width: 100%;
  height: 100%;
}
.savedJokes{
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
}
.savedJokesContainer{
  width: 40%;
}

.jokesDiv{
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 50px;
}

.jokesContainer{
  width: 40%;
}
.jokeCard{
  width: 100%;
  height: 250px;
  margin-top: 20px;
}

.addJokes{
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.addJokesContainer{
  width: 40%;
}

.topBar{
  margin-bottom: 50px;
}

.botFooter{
  width: 100%;
  background-color: red;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3F51B5;
  color:white
}

.aboutDiv{
  text-align: center;
}

.contactDiv{
  margin-top: 100px;
}

@media only screen and (max-width: 600px) {
 
  .jokesContainer{
    width: 100%;
    height: 100%;
  }
  .addJokesContainer{
    width:100%;
    height: 100%;
  }
  .savedJokesContainer{
    width: 100%;
  }
}
